import React, { useState, useEffect } from 'react';
import { OverlayTrigger, Tooltip, Form } from 'react-bootstrap';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import {  useDispatch } from 'react-redux';
import { GOOGLE_TRACKING, GOOGLE_TRACKING_CODE } from '../../../../../reducer/keys';
import { UPDATE_UTM_TRACKER } from '../../../../../actions/websiteAction';

const GoogleAnalytics = ({ wallData, save_and_publish, is_utm_tracker_change  }) => {

    const dispatch = useDispatch();  
    const [trackingIdError, setTrackingIdError] = useState(false) 

    const gtStatus = wallData?.wall?.ThemeInfo?.gtStatus
    const gtCode = wallData?.wall?.ThemeInfo?.gtCode

    const applyUTM_setting = () => {
      if (gtCode.trim().length == 0 && gtStatus == 1) { setTrackingIdError(true); return false }
      // let utmStatus = Wall?.utm_status
      // let utmSource = Wall?.utm_source
      // let utmMedium = Wall?.utm_medium
      // let utmCampaign = Wall?.utm_campaign
      dispatch(UPDATE_UTM_TRACKER(gtCode, gtStatus))
    }

    useEffect(() => {
   if(save_and_publish==true && is_utm_tracker_change==true) applyUTM_setting()
    }, [save_and_publish]);

    return <div className="mt-4">
            <div className="f-between">
              <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`google_anly`}>
                  <span className='me-2'>Google Analytics</span>
                  <OverlayTrigger placement='top' overlay={<Tooltip>Allow Google Analytics tracking for this website.</Tooltip>} >
                      <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'text-muted'}/></span>
                  </OverlayTrigger>
              </Form.Label>
              <Form.Check type="switch" id={`google_anly`} checked={gtStatus} onChange={(e) => {setTrackingIdError(false); dispatch({type:GOOGLE_TRACKING, payload:e.target.checked})}} />
              </div>
              <div className="mt-4">
                <Form.Control placeholder="e.g. UA-123456-1 A" className={`${trackingIdError ? 'is-invalid' :''}`}  value={gtCode} maxLength="100" type="text" onChange={(e) => {setTrackingIdError(false); dispatch({type:GOOGLE_TRACKING_CODE, payload:e.target.value})}} disabled={!gtStatus ? true : false}/>
                {trackingIdError ? <span className="invalid-feedback"> Please Fill Tracking ID </span>:''}
              </div>
            </div>
};

export default GoogleAnalytics;
