import { Component } from "react";
import { Row, Col, Form, ToggleButtonGroup, ToggleButton } from "react-bootstrap";
import Select from "react-select";
import ColorPicker from "../../CommonComponents/ColorPicker.js";
import { updateCardStyle, updateCtastatus } from "../../../../../actions/appsMoreAction.js";
import { connect } from "react-redux";
import InheritSwitch from './Element/InheritSwitch'
import RangeInput from "../../CommonComponents/RangeInput";
import { CARD_STYLE_ATHOR_DETAIL, CARD_STYLE_AUTHOR_COLOR, CARD_STYLE_CARD_COLOR, CARD_STYLE_CORNER_RADIUS, CARD_STYLE_FONT_COLOR, CARD_STYLE_FONT_LINK, CARD_STYLE_FONT_SIZE, CARD_STYLE_FONT_VARIENT, CARD_STYLE_HIDE_CONTENT, CARD_STYLE_HOVER_COLOR, CARD_STYLE_HOVER_EFFECT, CARD_STYLE_HOVER_TEXT, CARD_STYLE_POPUP_CONTENT, CARD_STYLE_POSITION, CARD_STYLE_REELS_VIEW, CARD_STYLE_SHARE_ACTION, CARD_STYLE_SHARE_OPTION, CARD_STYLE_SOCIAL_ICON_COLOR, CARD_STYLE_SOCIAL_ICON_COLOR_TYPE, CARD_STYLE_TIME, IS_CARD_STYLE_CHANGE } from "../../../../../reducer/keys.js";
import ErrorFound from "../../../../ErrorFound.js";
import SelectFont from "../Components/SelectFont.js";
import { postHighLightOptions } from "../../../../../utils/index.js";
class CardStyle extends Component {

  state = {
    spinner: false,
  };

  onChangeVariant = (e) => {
    this.props.CARD_STYLE_FONT_VARIENT(e.label.key)
  }

  onChangeHighlightPosition = (e) => {
    this.props.CARD_STYLE_POSITION(e.value)
  }

  componentDidUpdate(prevProps) {
    if (this.props.save_and_publish === true && prevProps.save_and_publish !== true) {
      if (this.props.is_card_style_change) {
        setTimeout(() => {
          this.onSubmitData();
        }, 500);
      }
    }
  }

  onFontChange = (event) => {
    const { allFont, wallData } = this.props
    const selectedFont = allFont.find(font => font.family == event.label.key)
    let selectVariant = JSON.parse(selectedFont.variants).find(item => item == wallData.CardStyle.variant)
    this.props.CARD_STYLE_FONT_LINK(selectedFont.family, selectVariant ? selectVariant : JSON.parse(selectedFont.variants)[0])
  }

  onSubmitData = (e) => {

    const { wallData } = this.props;
 
    const data = {
      link_font: wallData.CardStyle.style.font === undefined ? '' : wallData.CardStyle.style.font.split(' ').join('+'),
      css_font: wallData.CardStyle.style.font === undefined ? '' : wallData.CardStyle.style.font,
      font_varient: wallData.CardStyle.style.variant === undefined ? '' : wallData.CardStyle.style.variant,
      cardColor: wallData.CardStyle.style.background,
      fontColor: wallData.CardStyle.style.color,
      fontSize: wallData.CardStyle.style.size,
      roundEdge: wallData.CardStyle.style.radius,
      authorColor: wallData.CardStyle.author.color,
      iconType: wallData.CardStyle.icon.default,
      iconColor: wallData.CardStyle.icon.color,
      postTime: Number(wallData.CardStyle.timeStatus),
      inheritStyles: Number(wallData.CardStyle.inheritStyles),
      postAuthor: Number(wallData?.CardStyle.author.status),
      ctaStatus: Number(wallData.CardStyle.globalCTA.status),
      shareOption: Number(wallData?.CardStyle.shareStatus),
      socialAction: Number(wallData?.CardStyle.actionStatus),
      hideContent: Number(wallData.CardStyle.content.status) ? 0 : 1,
      popupContent: Number(wallData?.CardStyle.content.modalContentStatus),
      themeRuleId: wallData.ThemeInfo.id, 
      ownerId: localStorage.getItem('ownerId'),
      highlightPosition: wallData.ThemeStyle.direction,
      postHover: Number(wallData.CardStyle.style.mode),
      shoppingText: wallData.CardStyle.shopText,
      showReelsView: wallData.CardStyle.reelViewStatus ? 1 : 0
    }

    updateCardStyle({ data, wallId: wallData.ThemeInfo.wallId ? wallData.ThemeInfo.wallId : localStorage.getItem('active_wall'), themeId: wallData.ThemeInfo.id, personalizationId: wallData.ThemeStyle.id }).then(res => {
      this.setState({ spinner: false })
    }).catch((error) => console.warn(error))

  }

  onHoverColorChange = (id) => {
    this.props.CARD_STYLE_HOVER_COLOR(id)
    this.props.CARD_STYLE_SOCIAL_ICON_COLOR(id ? "#ffffff" : "#000000")
    window.$(`#ThemeRuleIconColor`).minicolors('value', id ? "#ffffff" : "#000000")
    this.props.CARD_STYLE_FONT_COLOR(id ? "#ffffff" : "#000000")
    window.$(`#ThemeRuleFontColor1`).minicolors('value', id ? "#ffffff" : "#000000")
  }

  render() {
    const { inheritStyle, wallData, is_card_style_change } = this.props;

    return (<>
      <ErrorFound><InheritSwitch /></ErrorFound>
      <div className="mt-4">
        <p className="h5">General Style</p>
        <SelectFont inheritStyle={inheritStyle} fontChange={this.onFontChange} variantChange={this.onChangeVariant} fontval={wallData?.CardStyle?.style?.font} vrntVal={wallData?.CardStyle?.style?.variant} />
      </div>
      {[4, 16, 50, 53, 55, 19, 47, 61, 62].includes(wallData?.ThemeInfo.id) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="font_color">Font Color</Form.Label>
        <ColorPicker id="font_color" isDisabled={inheritStyle ? "disabled" : ""} defaultColor={wallData?.CardStyle?.style.color} changeColor={(clr) => this.props.CARD_STYLE_FONT_COLOR(clr)} colorChanged={is_card_style_change} inheritStyle={inheritStyle} />
      </div> : null}

      {[4, 16, 50, 53, 55, 19, 47, 61, 62, 3].includes(wallData?.ThemeInfo.id) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="font_size">Font Size</Form.Label>
        <RangeInput inheritStyle={inheritStyle} id="font_size" min={10} max={50} step={1} defaultValue={wallData?.CardStyle?.style.size} value={wallData?.CardStyle?.style.size} onChange={(event) => { this.props.CARD_STYLE_FONT_SIZE(event) }} />
      </div> : null}

      {[16, 53, 61, 62].includes(wallData?.ThemeInfo.id) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="radius_range">Card Radius</Form.Label>
        <RangeInput id="radius_range" min={0} max={50} step={1} defaultValue={wallData?.CardStyle?.style.radius} value={wallData?.CardStyle?.style.radius} onChange={(event) => { this.props.CARD_STYLE_CORNER_RADIUS(event) }} />
      </div> : null}

      {[3, 19, 47, 61, 62].includes(wallData?.ThemeInfo.id) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="card_color">Card Colors</Form.Label>
        <ColorPicker id="card_color" defaultColor={wallData?.CardStyle.style.background} changeColor={(clr) => { this.props.CARD_STYLE_CARD_COLOR(clr) }} colorChanged={is_card_style_change} />
      </div> : null}

      {[53].includes(wallData?.ThemeInfo.id) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="post_position">Position (Post Highlight)</Form.Label>
        <Select id="post_position" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={postHighLightOptions} value={postHighLightOptions.find(item => item.value == wallData.ThemeStyle.direction)} onChange={(e) => this.onChangeHighlightPosition(e)} />
      </div> : null}


      {[4, 16, 50, 53, 55, 61].includes(wallData?.ThemeInfo.id) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="hover_text">Hover Text</Form.Label>
        <Form.Control id="hover_text" value={wallData?.CardStyle?.shopText} maxLength="30" onChange={e => { this.props.CARD_STYLE_HOVER_TEXT(e.target.value) }} />
      </div> : null}

      {[16, 53, 61].includes(wallData?.ThemeInfo.id) ? <div className="mt-4">
        <Form.Label className="mb-2" htmlFor="hover_text">Hover Color</Form.Label>
        <Row>
          <Col>
            <div className={`border pt-3 ps-3 rounded-1 cursor-pointer overlfow-hidden ${wallData?.CardStyle?.style.mode == 0 ? 'border-primary' : ''}`} onClick={(e) => { this.onHoverColorChange(0) }}>
              <div className='bg-white mt-3 ms-3 p-3 fw-medium fs-9' style={{ borderTopLeftRadius: 4, borderBottomRightRadius: 2 }}>White</div>
            </div>
          </Col>
          <Col>
            <div className={`border pt-3 ps-3 rounded-1 cursor-pointer overlfow-hidden ${wallData?.CardStyle?.style.mode == 1 ? 'border-primary' : ''}`} onClick={(e) => { this.onHoverColorChange(1) }}>
              <div className='bg-dark mt-3 ms-3 p-3 fw-medium text-gray-100 fs-9' style={{ borderTopLeftRadius: 4, borderBottomRightRadius: 2 }}>Black</div>
            </div>
          </Col>
        </Row>
      </div> : null}
      {[16, 61].includes(wallData?.ThemeInfo.id) ?
        <div className="mt-4">
          <Form.Label className="mb-2" htmlFor="hover_effect">Hover Effect</Form.Label>
          <ToggleButtonGroup className="w-100 mb-3" type="radio" name="options" value={wallData?.CardStyle?.content.status} onChange={(e) => { this.props.CARD_STYLE_HOVER_EFFECT(Number(e)) }}>
            <ToggleButton variant='secondary' id="h_e_caption" value={1}>
              Caption
            </ToggleButton>
            <ToggleButton variant='secondary' id="h_e_cta" value={0}>
              CTA
            </ToggleButton>
          </ToggleButtonGroup>
        </div> : null}

      <hr className=""></hr>


      <div>
        {!([61, 62].includes(wallData?.ThemeInfo.id)) ?
          <p className="h5">Author Style</p>
          : null}


        {[3, 19, 47, 63, 64].includes(wallData?.ThemeInfo.id) ? <div className="mt-4">
          <Form.Label className="mt-2" htmlFor="author_color">Author Color</Form.Label>
          <ColorPicker id="author_color" defaultColor={wallData?.CardStyle?.author.color} changeColor={(clr) => { this.props.CARD_STYLE_AUTHOR_COLOR(clr) }} inheritStyle={inheritStyle} colorChanged={is_card_style_change} />
        </div> : null}

        {[3, 47, 19, 61, 16, 53, 4, 50, 55].includes(wallData?.ThemeInfo.id) ? <>
          <div className="mt-4">
            <Form.Check type="switch" id="default_icon_color" className="f-between mb-2" label="Default Social Icon Color" checked={wallData?.CardStyle?.icon.default == 505 ? true : false} onChange={e => { this.props.CARD_STYLE_SOCIAL_ICON_COLOR_TYPE(e.target.checked ? 505 : 1) }} reverse />
          </div>
          <div style={{ display: wallData?.CardStyle.icon.default == 1 ? 'block' : 'none' }}>
            <ColorPicker id="icon_color" defaultColor={wallData?.CardStyle?.icon.color} changeColor={(clr) => { this.props.CARD_STYLE_SOCIAL_ICON_COLOR(clr) }} colorChanged={is_card_style_change} />
          </div>
        </> : null}

        {[3, 16, 19, 47, 53, 61, 62, 63, 64].includes(wallData?.ThemeInfo.id) ?
          <>
            {[3, 19, 47, 63, 64].includes(wallData?.ThemeInfo.id) ?
              <>
                <div className="mt-5">
                  <Form.Check type="switch" id="time_" className="f-between" label="Time" checked={wallData?.CardStyle.timeStatus} onChange={(e) => { this.props.CARD_STYLE_TIME(Number(e.target.checked)) }} reverse />
                </div>
                <div className="mt-5">
                  <Form.Check type="switch" id="author_details" className="f-between" label="Author Detail" checked={wallData?.CardStyle.author.status} onChange={(e) => { this.props.CARD_STYLE_ATHOR_DETAIL(Number(e.target.checked)) }} reverse />
                </div>
                {!([63, 64].includes(wallData?.ThemeInfo.id)) ? <div className="mt-5">
                  <Form.Check type="switch" id="share_option" className="f-between" label="Share Options" checked={wallData?.CardStyle.shareStatus} onChange={(e) => { this.props.CARD_STYLE_SHARE_OPTION(Number(e.target.checked)) }} reverse />
                </div> :null}
              </>
              : null}
            {[19, 47, 63, 64].includes(wallData?.ThemeInfo.id) ?
              <div className="mt-5">
                <Form.Check type="switch" id="hide_text" className="f-between" label="Hide Text" checked={wallData?.CardStyle.content.status} onChange={(e) => { this.props.CARD_STYLE_HIDE_CONTENT(Number(e.target.checked)) }} reverse />
              </div> : null}
          </> : null}
        {!([63, 64].includes(wallData?.ThemeInfo.id)) ? <div className="mt-5">
          <Form.Check type="switch" id="social_actions" className="f-between" label="Social Actions" checked={wallData?.CardStyle.actionStatus} onChange={(e) => { this.props.CARD_STYLE_SHARE_ACTION(Number(e.target.checked)) }} reverse />
        </div> :null}

        <div className="mt-5">
          <Form.Check type="switch" id="popup_content" className="f-between" label="Popup Content" checked={wallData?.CardStyle.content.modalContentStatus} onChange={(e) => { this.props.CARD_STYLE_POPUP_CONTENT(Number(e.target.checked)) }} reverse />
        </div>

        {[62, 61].includes(wallData?.ThemeInfo.id) ? <div className="mt-5">
          <Form.Check type="switch" id="reel_count" className="f-between" label="Reel View Count" checked={wallData?.CardStyle.reelViewStatus} onChange={(e) => { this.props.CARD_STYLE_REELS_VIEW(Number(e.target.checked)) }} reverse />
        </div> : null}
      </div>

    </>
    );
  }
}


const stateToProps = (state) => { 
  return { 
    showCTAButton: state.appsMore?.CardStyle?.globalCTA,
    videoPopup: state.appsMore.video_popup,
    is_card_style_change: state.appData?.is_card_style_change,
    inheritStyle: state.appData?.wall?.CardStyle?.inheritStyles,
    allFont: state.allFont?.allFonts,
    appData: state.appData
  }
}

const DispatchToProps = dispatch => {
  return {
    updateCtastatus: (data) => dispatch(updateCtastatus(data)),
    CARD_STYLE_FONT_LINK: (font, varient) => { dispatch({ type: CARD_STYLE_FONT_LINK, font: font, varient: varient }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_FONT_VARIENT: (data) => { dispatch({ type: CARD_STYLE_FONT_VARIENT, varient: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_FONT_SIZE: (data) => { dispatch({ type: CARD_STYLE_FONT_SIZE, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_CARD_COLOR: (data) => { dispatch({ type: CARD_STYLE_CARD_COLOR, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_FONT_COLOR: (data) => { dispatch({ type: CARD_STYLE_FONT_COLOR, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_HOVER_COLOR: (data) => { dispatch({ type: CARD_STYLE_HOVER_COLOR, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_HOVER_EFFECT: (data) => { dispatch({ type: CARD_STYLE_HOVER_EFFECT, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_HOVER_TEXT: (data) => { dispatch({ type: CARD_STYLE_HOVER_TEXT, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_CORNER_RADIUS: (data) => { dispatch({ type: CARD_STYLE_CORNER_RADIUS, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_POSITION: (data) => { dispatch({ type: CARD_STYLE_POSITION, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },

    CARD_STYLE_AUTHOR_COLOR: (data) => { dispatch({ type: CARD_STYLE_AUTHOR_COLOR, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_SOCIAL_ICON_COLOR_TYPE: (data) => { dispatch({ type: CARD_STYLE_SOCIAL_ICON_COLOR_TYPE, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_SOCIAL_ICON_COLOR: (data) => { dispatch({ type: CARD_STYLE_SOCIAL_ICON_COLOR, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_TIME: (data) => { dispatch({ type: CARD_STYLE_TIME, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_ATHOR_DETAIL: (data) => { dispatch({ type: CARD_STYLE_ATHOR_DETAIL, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },

    CARD_STYLE_SHARE_OPTION: (data) => { dispatch({ type: CARD_STYLE_SHARE_OPTION, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_SHARE_ACTION: (data) => { dispatch({ type: CARD_STYLE_SHARE_ACTION, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_HIDE_CONTENT: (data) => { dispatch({ type: CARD_STYLE_HIDE_CONTENT, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_POPUP_CONTENT: (data) => { dispatch({ type: CARD_STYLE_POPUP_CONTENT, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },
    CARD_STYLE_REELS_VIEW: (data) => { dispatch({ type: CARD_STYLE_REELS_VIEW, payload: data }); dispatch({ type: IS_CARD_STYLE_CHANGE, payload: true }); },

  }
}

export default connect(stateToProps, DispatchToProps)(CardStyle)