import { useEffect, useState } from "react";
import CodeMirror from "@uiw/react-codemirror";
import { css as codeCSS } from "@codemirror/lang-css"
import { Accordion } from "react-bootstrap";
import BackgroundStyle from "./BackgroundStyle";
import { useDispatch } from "react-redux";
import { updateCutomCSS } from "../../../../../actions/appsMoreAction";
import ThemeSetting from "./ThemeSetting";
import UGCUploads from "./UGCUploads";
import CardStyle from "./CardStyle";
import { TOAST_UPDATE, TOAST_LOADING } from "../../../../../utils";
import { ACTIVE_VIDEO_POPUP } from "../../../../../reducer/keys";
import { SET_CUSTOM_CSS } from "../../../../../reducer/keys";
import GoogleAnalytics from "./GoogleAnalytic";
import ErrorFound from "../../../../ErrorFound";
import CssSwitch from "./Element/CssSwitch";
import HeaderStyle from "./HeaderStyle";
import { loadLanguage } from '@uiw/codemirror-extensions-langs';
import { createTheme } from '@uiw/codemirror-themes';
import { tags as t } from '@lezer/highlight';


const Customization = (props) => {
  const { openBanner, a_key,  appData, fonts, userData, onCtaChange } = props
  const [cssOn, setcssOn] = useState(false)
  const dispatch = useDispatch()
  const { wall } = appData

  let backgroundDetails = wall;
  let onsite = wall?.SnapUp;
  let PersonalizationID = wall?.ThemeStyle?.id // key not exist

  useEffect(() => {

    dispatch({ type: ACTIVE_VIDEO_POPUP, payload: 'banner' })

    window.$("#wallThemeRuleCtaStatus").on('change', function (e) {
      onCtaChange()
    })

    window.$("#wallcssStatus").on('change', function (e) {
      setcssOn(this.value == 'true' ? true : false)
    })
  }, [cssOn])

  const applyCss = () => {
    const toastId = TOAST_LOADING()
    console.log('sdfjkljsdjfkjsdkj',wall?.ThemeStyle.css.code)
    updateCutomCSS(wall?.ThemeStyle.css.code, PersonalizationID).then(res => {
      const { data } = res;
      if (data.status) {
        TOAST_UPDATE(toastId, "success", data.message)
        // TOAST_SUCCESS(data.message)
      }
      else TOAST_UPDATE(toastId, "error", data.message)
      // openBanner()
    }
    ).catch(err => {
      console.log('err', err)
      TOAST_UPDATE(toastId, "error", "Error in updating")
    })
  }


  const myTheme = createTheme({
    theme: 'light',
    settings: {
      background: '#f8f8f8',
      caret: 'none',
      gutterBackground: '#f7f7f7',
    },
    styles: [
      { tag: t.comment, color: '#b06213' },
      { tag: t.variableName, color: '#ff0000' },
      { tag: [t.string, t.special(t.brace)], color: '#b83b3b' },
      { tag: t.number, color: '#166948' },
      { tag: t.bool, color: '#92349f' },
      { tag: t.null, color: '#166948' },
      { tag: t.keyword, color: '#166948' },
      { tag: t.operator, color: '#166948' },
      { tag: t.className, color: '#ff0000' },
      { tag: t.definition(t.typeName), color: '#b83b3b' },
      { tag: t.typeName, color: '#b83b3b' },
      { tag: t.angleBracket, color: '#92349f' },
      { tag: t.tagName, color: '#4d9840' },
      { tag: t.attributeName, color: '#92349f' },
      { tag: t.color, color: '#3a2ba4' },
      { tag: t.color, color: '#3a2ba4' },
    ],
  });

  useEffect(() => {
    if (appData?.save_and_publish == true && appData?.is_custome_css_change == true) applyCss();
  }, [appData?.save_and_publish]);

  return (
  <Accordion className="border-top" defaultActiveKey="background" flush>
   <Accordion.Item eventKey="background">
      <Accordion.Header>Background</Accordion.Header>
      <Accordion.Body className="bg-gray-100">
        <ErrorFound>
          <BackgroundStyle backgroundDetails={backgroundDetails} save_and_publish={appData?.save_and_publish} is_backgroun_change={appData?.is_backgroun_change} PersonalizationID={PersonalizationID} bannerDetail={wall} />
        </ErrorFound>
      </Accordion.Body>
    </Accordion.Item>
 
    {[63,64].includes(wall?.ThemeInfo?.id) ? <Accordion.Item eventKey="header_style">
      <Accordion.Header>Header Style</Accordion.Header>
       <Accordion.Body className="bg-gray-100">
         {fonts && fonts.length && wall && Object.keys(appData).length > 0 ?  
          <ErrorFound><HeaderStyle save_and_publish={appData?.save_and_publish} bannerDetail={wall} ownerId={wall?.ThemeInfo?.ownerId} fonts={fonts} /></ErrorFound>
          : null}
      </Accordion.Body>
    </Accordion.Item> : null}  

    <Accordion.Item eventKey="card_style"> 
      <Accordion.Header>Card Style</Accordion.Header>
      <Accordion.Body className="bg-gray-100">
        {fonts && fonts.length && appData?.wall && Object.keys(appData).length > 0 ?
          <ErrorFound><CardStyle save_and_publish={appData?.save_and_publish} wallData={wall} ownerId={wall?.ThemeInfo?.ownerId} fonts={fonts} planID={wall?.UserPlan?.planId} /></ErrorFound>
          : null}
      </Accordion.Body>
    </Accordion.Item>  

    <Accordion.Item eventKey="more_actions">
      <Accordion.Header>More Actions</Accordion.Header>
      <Accordion.Body className="bg-gray-100">
        <ErrorFound> <ThemeSetting planID={wall?.UserPlan?.planId} appData={appData} save_and_publish={appData?.save_and_publish} is_theme_setting_change={appData?.is_theme_setting_change} bannerDetail={wall} db_table={wall?.UserPlan?.db_table} adminId={wall} userData={userData} closePopup={openBanner} /></ErrorFound>
      </Accordion.Body>
    </Accordion.Item>  

    {(wall?.UserRule?.customCss != 0) ?
      <Accordion.Item eventKey={a_key ? a_key : 'banner'}>
        <Accordion.Header>Custom CSS</Accordion.Header>
        <Accordion.Body className="bg-gray-100">
          <CssSwitch />
          <div className={`${wall?.ThemeStyle?.css.status ? '' : 'inActiveCustomeCss'}`} >
            <ErrorFound> <CodeMirror value={wall?.ThemeStyle?.css.code} height="350px" save_and_publish={appData?.save_and_publish} theme={myTheme}  basicSetup={{ foldGutter: false, dropCursor: false, allowMultipleSelections: false, indentOnInput: false, autocompletion: false }} extensions={[loadLanguage('css')]} onChange={(e) => { dispatch({ type: SET_CUSTOM_CSS, payload: e }) }} editable={wall?.ThemeStyle?.css.status ? true : false} /></ErrorFound>
          </div>
        </Accordion.Body>
      </Accordion.Item> : null}
     <Accordion.Item eventKey="google_analytics">
      <Accordion.Header>Google Analytics</Accordion.Header>
      <Accordion.Body className="bg-gray-100">
        <ErrorFound><GoogleAnalytics wallData={appData} save_and_publish={appData?.save_and_publish} is_utm_tracker_change={appData?.is_utm_tracker_change} /></ErrorFound>
      </Accordion.Body>
    </Accordion.Item>
     {appData.onsite_status && !([63,64].includes(wall?.ThemeInfo.id))?
      <Accordion.Item eventKey="snap_up">
        <Accordion.Header>Review Hub settings</Accordion.Header>
        <Accordion.Body className="bg-gray-100">
          <ErrorFound><UGCUploads SnapUp={onsite} save_and_publish={appData?.save_and_publish} is_magic_upload_change={appData?.is_magic_upload_change} PersonalizationID={PersonalizationID} /></ErrorFound>
        </Accordion.Body>
      </Accordion.Item> : null}  
   </Accordion>
  );
};




export default Customization;