import { useRef, useState } from "react";
import Select from "react-select";
import { Form, OverlayTrigger, Tooltip } from "react-bootstrap";
import RangeInput from "../../CommonComponents/RangeInput";
import { FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { useEffect } from "react";
import {  useDispatch } from 'react-redux';
import { updateThemeSetting } from "../../../../../actions/appsMoreAction";
import { COLUMNLIST, MOBILE_COLUMNLIST } from "../../../../../utils";
import DateTimeField from '@1stquad/react-bootstrap-datetimepicker';
import '@1stquad/react-bootstrap-datetimepicker/css/bootstrap-datetimepicker.css';
import { SETTING_AUTO_SLIDER, SETTING_VIDEO_PLAY, SETTING_BEST_FIT, SETTING_CAROUSEL_POST, SETTING_COLUMNS, SETTING_COLUMNS_MOBILE, SETTING_MINIMUM_POSTWIDTH, SETTING_NO_OF_ROW, SETTING_POPUP_SLIDER, SETTING_POST_NUMBER, SETTING_POST_SPACING, SETTING_SHOW_MORE_BTN, SETTING_TIME_PER_SLIDER, SETTING_TRIM_CONTENT, SETTING_POST_TEXT, SETTING_HIDE_POST, SETTING_HIDE_OLDER_POST } from "../../../../../reducer/keys";
import moment from 'moment';
import ToggleButton from "./Element/ToggleButton"
import { GET_ALL_WEBSITE_LIST, CHANGE_WEBSITE_WALL } from "../../../../../actions/websiteAction";

const ThemeSetting = (props) => {
  
  const { ThemeInfo, ThemeStyle, CardStyle } = props.bannerDetail
  const themeId = ThemeInfo.id
  
  const dispatch = useDispatch()
  const [rowSelect, setRowSelect] = useState([
    { label: "1 Row", value: 1 },
    { label: "2 Rows", value: 2 },
  ]);

  const date = ThemeStyle.oldPost.showFrom
  const myRefname = useRef(null);
  const customSlelect = [
    { label: "One Day Ago", value: 1 },
    { label: "Two Days Ago", value: 2 },
    { label: "Three Days Ago", value: 3 },
    { label: "Four Days Ago", value: 4 },
    { label: "Five Days Ago", value: 5 },
    { label: "Six Days Ago", value: 6 },
    { label: "Seven Days Ago", value: 7 },
    { label: "One Month", value: 30 },
    { label: "Custom Select", value: 8 },
  ];

  var d = new Date(0); // The 0 there is the key, which sets the date to the epoch
  d.setUTCSeconds(date == 0 ? 1641015000 : date);
  var minimalDate = moment(d).format("MM DD YYYY hh:mm");

  useEffect(() => {
    if (props.save_and_publish == true && props.is_theme_setting_change == true) {
      setTimeout(() => { submitData(); }, 500);
    }
  }, [props.save_and_publish]);

  const submitData = () => {
    let data = {
      websiteId: localStorage.getItem('website_id'),
      wallId: localStorage.getItem('active_wall'),
      ownerId: ThemeInfo?.ownerId,
      db_table: props.db_table,
      p_id: ThemeStyle.id,
      carouselPost: Number(ThemeStyle.carouselPost),
      cssStatus: ThemeStyle?.css.status,
      randomizePost: 0,
      loadMoreStatus: Number(ThemeStyle?.loadMoreStatus),
      autoScrollStatus: Number(ThemeStyle?.autoScrollStatus),
      postText: Number(ThemeStyle?.textPostStatus),
      mobilePopup: Number(ThemeStyle?.modal.mobile),
      trimcontent: Number(CardStyle?.content.trimcontent),
      popupSlideShow: Number(ThemeStyle.modal.slide),
      textDecorate: Number(CardStyle?.style.textDecoration),
      customUrl: `${ThemeStyle?.modal.redirect}`,
      numberOfPosts: ThemeStyle?.totalPosts,
      hidePosts:  ThemeStyle.oldPost.status,
      showHideOlderPost: ThemeStyle.oldPost.showFrom,
      showHideOldPost:  ThemeStyle.oldPost.status,
      minimumPostWidth: CardStyle?.minimumWidth,
      padding: CardStyle?.style.padding,
      theme_id: ThemeInfo?.id,
      numberOfCoumn: ThemeStyle?.column.desktop,
      mobileColumn: ThemeStyle?.column.mobile,
      multiRow: ThemeStyle?.row,
      fitRow: Number(ThemeStyle?.bestFit),
      autoPlay: Number(ThemeStyle?.autoPlay),
      slidePost: Number(ThemeStyle?.slide.autoSlide),
      slideDuration: ThemeStyle?.slide.slideDuration,
      postFeatured: 1
    }

    if (ThemeStyle?.modal.action == 3 && validateUrl(ThemeStyle?.modal.redirect) || ThemeStyle?.modal.action != 3) {
      updateThemeSetting(data).then(res => {
        const { data } = res
        if (data.status) { dispatch(GET_ALL_WEBSITE_LIST()) }
      }).catch((error) => console.warn(error))
    }
  }


  const validateUrl = (value) => {
    return /^(?:(?:(?:https?|ftp):)?\/\/)(?:\S+(?::\S*)?@)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:[/?#]\S*)?$/i.test(value);
  }

  const columnOptions = COLUMNLIST(themeId)
  const mobColumnOptns = MOBILE_COLUMNLIST(themeId)

  return (<>
    <div className="mt-4">
      <ToggleButton toggleName="Popup 'Slider'" toggleValue={ThemeStyle.modal.slide} callbackFn={() => { }} callbackFn2={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_POPUP_SLIDER, payload: Number(status) })} tooltipTxt=" This feature lets you slide through the pop-up." />
    </div>

    <div className="mt-4">
      <ToggleButton toggleName="Carousel Post" callbackFn2={() => { }} toggleValue={ThemeStyle.carouselPost} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_CAROUSEL_POST, payload: Number(status) })} tooltipTxt="Enable to reflect carousal posts." />
    </div>

    {[16, 50, 61, 62].includes(themeId) ?
      <div className="mt-4">
        <ToggleButton toggleName="Auto Slide" callbackFn2={() => { }} toggleValue={ThemeStyle?.slide.autoSlide} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_AUTO_SLIDER, payload: Number(status) })} tooltipTxt="Enable the Gallery to automatically slide." />
      </div> : ''}

    {!([63,64]).includes(themeId) ?<div className="mt-4">
      <ToggleButton toggleName="Video AutoPlay" callbackFn2={() => { }} toggleValue={ThemeStyle?.autoPlay} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_VIDEO_PLAY, payload: Number(status) })} tooltipTxt="Enable to auto-play video previews in thumbnails." />
    </div> : null}
    {[3, 4, 19, 53, 63, 64].includes(themeId) ? <>
      <div className="mt-4">
        <ToggleButton toggleName="Show More Button" toggleValue={ThemeStyle?.loadMoreStatus} callbackFn={() => { }} callbackFn2={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_SHOW_MORE_BTN, loadMore: Number(status), autoLoad: Number(ThemeStyle?.autoScrollStatus ? false : ThemeStyle?.autoScrollStatus) })} tooltipTxt="Show/Hide the 'Show more' button." />
      </div>
      <div className="mt-4">
        <ToggleButton toggleName="Auto-Load" toggleValue={ThemeStyle?.autoScrollStatus} callbackFn={() => { }} callbackFn2={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_SHOW_MORE_BTN, autoLoad: Number(status), loadMore: Number(ThemeStyle?.loadMoreStatus ? false : ThemeStyle?.loadMoreStatus) })} tooltipTxt="Experience the never-ending trail of feeds." />
      </div> </> : ""}
    {[19].includes(themeId) ?
      <div className="mt-4">
        <ToggleButton toggleName="Hide 'Text Only' Posts" callbackFn2={() => { }} toggleValue={ThemeStyle?.textPostStatus} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_POST_TEXT, payload: status })} tooltipTxt="Hide 'Text Only' Posts" />
      </div> : ''}
    {[3, 19, 47, 63, 64].includes(themeId) ?
      <div className="mt-4">
        <ToggleButton toggleName="Trim Content" callbackFn2={() => { }} toggleValue={CardStyle?.content.trimcontent} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_TRIM_CONTENT, payload: Number(status) })} tooltipTxt="It will trim post text after a limit." />
      </div> : ''}
    {themeId == 4 ?
      <div className="mt-4">
        <ToggleButton toggleName="Best Fit" callbackFn2={() => { }} toggleValue={ThemeStyle?.bestFit} callbackFn={() => { }} dispatchfn={(status) => dispatch({ type: SETTING_BEST_FIT, payload: Number(status) })} tooltipTxt="Enables you to load images smoothly." />
      </div> : ''}

    <div className="mt-4">
      <Form.Label className="mb-2 d-flex flex-wrap" htmlFor={`total_post`}>
        <span className='me-2'>Total Number of Posts to Display</span>
        <OverlayTrigger placement='top' overlay={<Tooltip>Maximum no. of posts to be displayed on live gallery.</Tooltip>} >
          <span><FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'fs-8 text-muted'} /></span>
        </OverlayTrigger>
      </Form.Label>
      <Form.Control type="number" id={`total_post`} min="1" max="100" value={ThemeStyle?.totalPosts} onChange={(e) => { dispatch({ type: SETTING_POST_NUMBER, payload: e.target.value > 100 ? 100 : e.target.value <= 0 ? 1 : e.target.value }) }} />
    </div>

    {[50, 61, 16, 62, 61].includes(themeId) & ThemeStyle?.slide.autoSlide ?
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`time_slide`}>Time per slide ({ThemeStyle?.slide.slideDuration} second)</Form.Label>
        <RangeInput id="time_slide" min={1} max={10} step={1} value={ThemeStyle?.slide.slideDuration} onChange={(event) => { dispatch({ type: SETTING_TIME_PER_SLIDER, payload: event }) }} />
      </div> : null}
    {[4, 16, 47, 53, 61, 62].includes(themeId) ?
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`desktop_col`}>Column (Desktop) </Form.Label>
        <Select id="desktop_col" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={columnOptions}
          value={columnOptions[columnOptions.findIndex(i => i.value == ThemeStyle?.column.desktop)]}
          onChange={e => { dispatch({ type: SETTING_COLUMNS, payload: e.value ? e.value : 0 }); dispatch(CHANGE_WEBSITE_WALL(localStorage.getItem('active_wall'), e.value ? e.value : 10)) }} />
      </div> : null}

    {[4, 16, 47, 61, 62].includes(themeId) ?
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`mobile_col`}>Column (Mobile) </Form.Label>
        <Select id="mobile_col" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={mobColumnOptns} value={mobColumnOptns[mobColumnOptns.findIndex(i => i.value ==  ThemeStyle.column.mobile)]}
          onChange={e => { dispatch({ type: SETTING_COLUMNS_MOBILE, payload: e.value }) }} />
      </div> : null}

    {ThemeStyle?.row && themeId == 16 ?
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`no_of_row`}>No.of Rows ({ThemeStyle?.row})</Form.Label>
        <Select id="mobile_col" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" options={rowSelect} defaultValue={rowSelect[ThemeStyle?.row - 1]} onChange={e => { dispatch({ type: SETTING_NO_OF_ROW, payload: e.value }) }} />
      </div> : null}

    {![61, 62].includes(themeId) ?
      <div className="mt-4">
        <div className="mb-2">
          <ToggleButton toggleName="Hide Old Posts" dispatchfn={(status) => dispatch({ type: SETTING_HIDE_POST, payload: status })} toggleValue={ThemeStyle.oldPost.status} callbackFn={() => { }} callbackFn2={() => { }} tooltipTxt="Hides old posts on your live gallery." />
        </div>
        <div className="mb-2">
          {ThemeStyle.oldPost.status ? <Select id="hide_post_range" className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" menuPlacement="top" options={customSlelect} defaultValue={customSlelect[ThemeStyle.oldPost.showFrom == 30 ? 7 : ThemeStyle.oldPost.showFrom == 8 ? 8 : ThemeStyle.oldPost.showFrom - 1]} onChange={e => dispatch({ type: SETTING_HIDE_OLDER_POST, payload: e.value })} /> : null}
        </div>
        {ThemeStyle.oldPost.showFrom == 8 && ThemeStyle.oldPost.status ?
          <DateTimeField
            language={'en'}
            dateTime={date == 0 ? '' : minimalDate}
            inputFormat={'MM/DD/YYYY HH:mm A'}
            onChange={(e) => dispatch({ type: SETTING_HIDE_POST, payload: moment(e).format("MM/DD/YYYY hh:mm") })}
            viewMode={'date'}
            className="form-control form-control-sm"
            startOfWeek={'week'}
            ref={myRefname}
            defaultText="Select date & time"
            id="hide_post_date"
          /> : null}
      </div> : null}

    {[3, 19, 55, 63].includes(themeId) ?
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`min_post_w`}>Minimum Post Width ({CardStyle?.minimumWidth}px)</Form.Label>
        <RangeInput id="time_slide" min={150} max={500} step={1} value={CardStyle?.minimumWidth} onChange={(event) => { dispatch({ type: SETTING_MINIMUM_POSTWIDTH, payload: event }) }} />
      </div> : null}

    {[3, 4, 16, 19, 55, 61, 62, 63].includes(themeId) ?
      <div className="mt-4">
        <Form.Label className="mb-2" htmlFor={`min_post_w`}>Post Spacing ({CardStyle?.style.padding}px)</Form.Label>
        <RangeInput id="time_slide" min={0} max={50} step={1} value={CardStyle?.style.padding} onChange={(event) => { dispatch({ type: SETTING_POST_SPACING, payload: event }) }} />
      </div> : null}
  </>
  );
};

export default ThemeSetting;